.topic {
	position: relative;
	padding: 10.4493vh 0 200px;
	min-height: 100vh;
	@include lg {
		padding: 47px 0 55px;
		min-height: initial; }
	@include sm {
		padding: 70px 0 47px; }
	&__inner {
		padding-top: 9.4vh;
		@include lg {
			padding-top: 0; } }
	&__logo {
		float: left;
		padding: 16px 0 0;
		width: 150px;
		font-size: 0;
		text-align: center;
		@include lg {
			float: none;
			padding: 0 0 47px; }
		@include md {
			padding-bottom: 62px; }
		@include sm {
			padding-bottom: 34px;
			width: 90px;
			display: none; }
		img {
			max-width: 100%;
			@include lg {
				width: 68px; }
			@include sm {
				width: 42px; } } }
	&__title {
		margin-top: 28px;
		@include f(32, 37, null, 700);
		letter-spacing: 0.08em;
		@include lg {
			margin-top: 30px; }
		@include sm {
			margin-top: 13px;
			@include f(16, 19); } }
	&__content {
		overflow: hidden;
		padding: 0 103px 0 116px;
		@include lg {
			padding: 0; }
		@include sm {
			overflow: visible; } }
	&__sub-title {
		margin-bottom: 2.51vh;
		@include f(48, 58, null, 700);
		line-height: 1.22;
		letter-spacing: 0.009em;
		@include lg {
			margin-bottom: 33px;
			font-size: 60px; }
		@include md {
			margin-bottom: 29px;
			max-width: 600px;
			@include f(46, 54); }
		@include sm {
			margin-bottom: 12px;
			@include f(26, 30); } }
	&__description {
		max-width: 650px;
		font-size: 26px;
		line-height: 1.26;
		letter-spacing: -0.001em;
		@include lg {
			max-width: 463px;
			line-height: 1.4; }
		@include sm {
			@include f(16, 22); }
		p {
			padding: 10px 0;
			@include sm {
				padding: 5px 0; } } }
