	&__bottom {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 9.4vh;
		@include lg {
			position: relative;
			left: auto;
			right: auto;
			bottom: auto;
			margin-top: 50px; }
		@include md {
			margin-top: 9vh; }
		@include sm {
			margin-top: 24px; }
		.inner {
			display: table;
			width: 100%;
			@include lg {
				display: block;
				@include clr; } } }
	&__t-coll {
		vertical-align: bottom;
		display: table-cell;
		@include clr;
		width: 33.8%;
		&:first-child {
			@include sm {
				display: none; } }
		@include lg {
			float: left;
			width: auto; }
		@include sm {
			width: 28%; }
		& + .topic__t-coll {
			margin-top: 45px;
			width: 66.2%;
			@include lg {
				margin: 0 0 0 53px;
				width: auto; }
			@include sm {
				margin: 0;
				width: 100%; } }
		@include lg {
			display: block; } }
	.inner {
		@include lg {
			margin: 0 auto;
			max-width: 795px; }
		@include md {
			padding: 0 10px 0 80px;
			max-width: 768px; }
		@include sm {
			padding: 0 30px;
			max-width: 100%; }
		@include xxs {
			padding: 0 26px 0 31px; } } }

.info {
	position: relative;
	padding-left: 22px;
	vertical-align: top;
	letter-spacing: 0.17em;
	font-size: 12px;
	color: rgba($white, .5);
	text-transform: uppercase;
	display: inline-block;
	@include sm {
		padding: 0; }
	&__icon {
		position: absolute;
		top: -1px;
		left: 0;
		@include sm {
			position: relative;
			top: 0;
			text-align: center; }
		.icon {
			font-size: 20px; } }
	&__text {
		@include sm {
			margin: 0 auto;
			max-width: 205px; } } }
.creators {
	&__text {
		margin-top: 10px;
		@include f(12, 24);
		letter-spacing: 0.17em;
		text-transform: uppercase;
		@include md {
			margin-top: 0;
			display: block; } }
	&__text,
	&__list {
		vertical-align: top;
		display: inline-block;
		&:not(:first-child) {
			margin-left: 40px;
			@include md {
				margin-left: 0; } } }
	&__list {
		@include md {
			margin-top: 10px;
			display: block; } }
	&__item {
		vertical-align: middle;
		font-size: 0;
		display: inline-block;
		&:not(:first-child) {
			margin-left: 22px;
			@include sm {
				margin-left: 7px; } }
		&_text {
			font-size: 14px; } } }

.signup {
	margin-top: 9vh;
	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 50%;
		bottom: 0;
		display: none;
		width: 1000px;
		z-index: -1;
		background: rgba($black, 0.2);
		transform: translate3d(-50%,0,0);
		@include sm {
			display: block; } }
	@include lg {
		margin-top: 28px; }
	@include md {
		margin-top: 42px; }
	@include sm {
		position: relative;
		margin-top: 18px;
		padding: 21px 0 35px; }
	.btn {
		padding: 15px 70px 16px;
		@include sm {
			padding: 15px 16px 16px;
			width: 100%; }
		&:not(:first-child) {
			margin-top: 1.6vh;
			@include sm {
				margin-top: 16px; } } } }

.press {
	padding-bottom: 20px;
	@include lg {
		padding: 0 0 0 25px; }
	@include md {
		padding: 0; }
	@include sm {
		font-size: 14px; }
	&__text {
		max-width: 124px;
		@include lg {
			margin-bottom: 19px;
			max-width: 120px;
			display: block; }
		@include sm {
			max-width: 80px;
			line-height: 1.3;
			letter-spacing: -0.009em; }
		&:not(:last-child) {
			margin-right: 26px;
			@include lg {
				margin-right: 0; } } }
	&__text,
	&__wrap,
	&__col {
		vertical-align: top;
		display: inline-block; }
	&__col {
		@include sm {
			display: block; }
		&:not(:first-child) {
			margin-left: 51px;
			@include lg {
				margin-left: 20px; }
			@include sm {
				margin: 18px 0 0; } } }
	&__title {
		@include f(26, 30, null, 700);
		@include sm {
			font-size: 16px; } } }

.clouds {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	width: 100%;
	height: 1470px;
	perspective: 1000;
	-webkit-perspective: 1000;
	-moz-perspective: 1000;
	-o-perspective: 1000;
	.is-firefox & {
		perspective: 500px;
		-webkit-perspective: 500px;
		-moz-perspective: 500px;
		-o-perspective: 500px; }
	&__word {
		position: absolute;
		top: 0;
		left: 50%;
		margin-left: -850px;
		width: 1700px;
		height: 1470px;
		transform-style: preserve-3d;
		-webkit-transform-style: preserve-3d;
		-moz-transform-style: preserve-3d;
		-o-transform-style: preserve-3d; }
	[class^="cloudBase-"] {
		position: absolute;
		margin-left: -10px;
		margin-top: -10px;
		width: 20px;
		height: 20px;
		transform-style: preserve-3d;
		-webkit-transform-style: preserve-3d;
		-moz-transform-style: preserve-3d;
		-o-transform-style: preserve-3d; }
	.cloudBase-1 {
		top: 150px;
		left: 30%; }
	.cloudBase-2 {
		top: 200px;
		left: 40%; }
	.cloudBase-3 {
		top: 250px;
		left: 50%; }
	.cloudBase-4 {
		top: 200px;
		left: 60%; }
	.cloudBase-5 {
		top: 150px;
		left: 70%; }

	.cloudBase-6 {
		top: 830px;
		left: 20%; }
	.cloudBase-7 {
		top: 830px;
		left: 30%; }
	.cloudBase-8 {
		top: 830px;
		left: 60%; }
	.cloudBase-9 {
		top: 830px;
		left: 70%; }

	.cloudLayer {
		position: absolute;
		left: 50%;
		top: 50%;
		width: 256px;
		height: 256px;
		margin-left: -128px;
		margin-top: -128px;
		-webkit-transition: opacity .5s ease-out;
		-moz-transition: opacity .5s ease-out;
		-o-transition: opacity .5s ease-out; } }
