.sm-info {
	position: relative;
	padding: 9px 0 8px 30px;
	border-left: 4px solid #3EBDFF;
	@include lg {
		padding: 0 0 8px 30px; }
	@include sm {
		padding: 0 0 15px 9px;
		margin: 0 0 0 2px;
		font-size: 14px; }
	&__text {
		max-width: 184px;
		vertical-align: top;
		display: inline-block;
		@include lg {
			margin-bottom: 22px;
			max-width: 314px;
			display: block; }
		@include sm {
			padding-left: 94px;
			margin: 0;
			max-width: 80%;
			min-height: 62px;
			line-height: 1.3; }
		@include xxs {
			max-width: 100%; }
		a {
			color: #60C9FF; } }
	&__wrap {
		margin: 5px 0 0 37px;
		vertical-align: top;
		display: inline-block;
		@include lg {
			margin: 0; }
		@include sm {
			display: block; } }
	&__logo,
	&__indicators,
	&__col {
		vertical-align: top;
		display: inline-block; }
	&__logo {
		@include sm {
			position: absolute;
			top: 9px;
			left: 15px;
			width: 72px; }
		img {
			max-width: 100%;
			display: block; } }
	&__indicators {
		padding-top: 4px;
		@include lg {
			padding-top: 0; }
		@include sm {
			width: 100%;
			@include clr; }
		&:not(:first-child) {
			margin-left: 36px;
			@include lg {
				margin-left: 27px; }
			@include sm {
				margin: 11px 0 0; } } }
	&__col {
		max-width: 200px;
		@include lg {
			max-width: 143px; }
		@include sm {
			float: left;
			width: 95px;
			font-size: 14px; }
		&:not(:last-child) {
			margin-right: 19px;
			@include sm {
				margin: 0;
				float: right;
				max-width: 100%;
				width: calc(100% - 95px); } } }
	&__title {
		@include f(26, 30, null, 700);
		@include sm {
			font-size: 16px; } } }
