.introducing {
	padding: 83px 0 66px;
	background: #102F61;
	// background-image: linear-gradient(to right, #003064 0%, #011b37 100%)
	@include md {
		padding: 100px 0; }
	@include sm {
		padding: 43px 0; }
	&__title {
		@include f(40, 40);
		text-align: left;
		@include xl {
			text-align: center; }
		&:not(:last-child) {
			margin-bottom: 28px; } }
	&__description {
		max-width: 800px;
		@include f(20, 30);
		@include xl {
			margin: 0 auto;
			text-align: center; } }
	&__diagram {
		margin: 88px auto 0;
		padding: 0 15px;
		max-width: 1250px;
		@include sm {
			padding: 0; } }
	&__footer {
		padding-top: 36px;
		padding-bottom: 32px;
		font-size: 0;
		@include clr;
		@include xl {
			text-align: center; } }
	&__label {
		float: left;
		@include f(20, 30);
		@include xl {
			float: none; }
		@include sm {
			@include f(16, 20); } }
	&__logo {
		float: right;
		margin-right: 25px;
		@include xl {
			float: none;
			margin: 25px 0 0; } } }
