.features {
	padding: 49px 0 56px;
	@include md {
		padding: 53px 0;
		text-align: center; }
	@include sm {
		padding: 40px 0; }
	&__title {
		&:not(:last-child) {
			margin-bottom: 27px; } }
	&__sm-description {
		max-width: 800px;
		font-size: 21px;
		line-height: 30px;
		@include md {
			max-width: 100%; } }
	&__wrap {
		margin: 0 -15px;
		font-size: 0;
		@include clr;
		@include sm {
			margin: 0; }
		&:not(:first-child) {
			margin-top: 60px;
			@include md {
				margin-top: 98px; }
			@include sm {
				margin-top: 53px; } } }
	&__col {
		margin-bottom: 33px;
		padding: 0 15px;
		width: 33.3%;
		font-size: 16px;
		line-height: (22/16);
		display: inline-block;
		vertical-align: top;
		@include md {
			margin-bottom: 47px;
			float: none;
			width: 100%; }
		@include sm {
			margin-bottom: 28px;
			padding: 0;
			font-size: 14px;
			line-height: (22/14); }
		p {
			max-width: 330px;
			@include md {
				max-width: none; } } }
	&__ico {
		margin-bottom: 11px;
		height: 68px;
		@include md {
			vertical-align: top;
			display: inline-block; }
		&_stars {
			padding-top: 10px;
			width: 85px; }
		&_ppl {
			padding-top: 11px;
			width: 57px; }
		&_coins {
			padding-top: 11px;
			width: 53px; }
		&_robo {
			padding-top: 4px;
			width: 61px; }
		&_net {
			width: 68px; }
		&_clouds {
			padding-top: 14px;
			margin-left: 5px;
			width: 61px; }
		// &:not(:last-child)
		// 	margin-bottom: 25px
		// 	+md
		// 		margin-bottom: 14px
		// 	+sm
		// 		margin-bottom: 19px
		img {
			max-width: 100%;
			display: block; } }
	&__title-md {
		max-width: 360px;
		@include f(26, 30, null, 700);
		@include md {
			max-width: none; }
		&:not(:last-child) {
			margin-bottom: 20px;
			@include md {
				margin-bottom: 18px; }
			@include sm {
				margin-bottom: 21px;
				font-size: 20px; } } }
	&__formula {
		margin: 15px auto 0;
		max-width: 180px;
		img {
			max-width: 100%;
			display: block; } } }
